/*
 *  Featured Content 6
 *    Styles for the featured content 6 section.
 *
 *  Version:
 *    1.0.0 - 2021/11/11
 */

.c-featured-content-6 {
  background-color: $s-color--background-subdued;

  &__outer {
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
  }

  &__inner {
    padding: 2rem $s-gutter--edge-mobile 3.75rem;
  }

  &__image-wrapper {
    position: relative;
    width: 100%;
    padding-bottom: 26%;

    &.has-1-image {
      padding-bottom: 2rem;
    }
  }

  &__image-1-container {
    width: 53.5%;
    z-index:$s-z--text-overlay;
    padding-bottom: 70%;

  }

  &__image-2-container {
    position: absolute;
    width: 53.5%;
    right: 0;
    top: 19%;
    padding-bottom: 70%;
  }

  &__content {
    margin-left: 2.375rem;
  }

  &__title {
    margin: 1.25rem 0 1.625rem;
  }

  &__cta-container {
    margin-left: 2.375rem;
  }

  /*** Media Queries ***/
  @include t-media-query($s-small-up) {

    &__inner {
      padding: 2rem 2.75rem 6rem;
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      align-items: center;
    }

    &__image-wrapper {
      width: 50%;
      padding-bottom: 0;
    }

    &__content-container {
      width: 40%;
    }
  }

  @include t-media-query($s-medium-up) {

    &__inner {
      padding: 4rem 6% 10rem;
      gap: 2rem;
    }

    &__title {
      max-width: 31.25rem;
      margin-top: 1.0625rem;
      margin-bottom: 1.375rem;
    }

    &__content {
      margin-left: 14.5%;
      max-width: 25.625rem;
    }

    &__cta-container {
      margin-left: 14.5%;
    }

    &__image-2-container {
      top: 25%;
    }

    &__content-container {
      width: 48%;
      margin: 0 auto;
    }
  }

  @include t-media-query($s-large-up) {

    &__inner {
      padding: 4rem 6% 10rem;
      gap: 0;
    }
  }

  @include t-media-query($s-xlarge-up) {

    &__inner {
      padding-bottom: 19.6875rem;
    }

    &__content-container {
      margin-right: auto;
      margin-left: 8.5%;
    }

    &__image-wrapper {
      width: 55%;
    }

    &__title {
      margin-top: 6.875rem;
      margin-bottom: 2.875rem;
    }
  }
}